import React, { Suspense } from 'react';
import { Route, Switch } from 'react-router-dom';
import Dashboard from '../../views/pages/dashboard';
import LaporanStockGolbalProduksi from '../../views/pages/laporan/stock_global_produksi/laporanstockglobalproduksi';
import LaporanKirimSaldoTahun from '../../views/pages/laporan/kirim_saldo_tahunan/laporankirimsaldotahun';
import Login from '../../views/pages/login';
import NotFoundPage from '../../views/pages/notfoundpage';

// eslint-disable-next-line import/prefer-default-export
export const RouteLink = () => {
  return (
    <Suspense fallback={<div />}>
      <Switch>
        <Route path="/" exact component={Login} />
        <Route path="/dashboard" exact component={Dashboard} />
        <Route
          path="/laporan/stock-global-produksi"
          exact
          component={LaporanStockGolbalProduksi}
        />
        <Route
          path="/laporan/kirim-saldo-tahun"
          exact
          component={LaporanKirimSaldoTahun}
        />
        <Route path="*" component={NotFoundPage} />
      </Switch>
    </Suspense>
  );
};
